body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
input, label {
  display: block; }

h1 {
  color: #492; }

textarea {
  width: 90%;
  height: 60vh;
  white-space: pre-line;
  margin: 1rem auto; }

button {
  display: block;
  margin: 2rem; }

code {
  white-space: pre-wrap;
  color: blue;
  background: #000;
